<script>
import $ from 'jquery'
// @ is an alias to /src
import { isMobile, isTablet } from '@/utils'

export default {
  name: 'section1',

  data() {
    return {
      isMobile,
      isTablet
    }
  },

  methods: {
    fnMLeft() {}
  },

  created() {},

  mounted() {
    $(window).resize(() => {
      console.log('resize')
    })
    if (!isMobile) {
      console.log('=========PC=========')
      const ww = ($(window).width() + 300) * 0.5625
      console.log('ww', ww)
      $('.s3pic').css('height', ww)
      let mStart = 0
      $('.s3box').mousedown(e => {
        if (event.which === 1) {
          if (mStart === 0) {
            mStart = e.pageX
          }
          $('.s3box').addClass('move')
          console.log('ok')
        }
      })
      $('.s3box').mouseup(event => {
        $('.s3box').removeClass('move')
        console.log('up')
      })

      $('#sec3').on('mousemove', '.s3box.move', e => {
        let move = e.pageX - mStart
        switch (true) {
          case move <= -150:
            move = -150
            break
          case move >= 150:
            move = 150
            break
          default:
        }
        const trans = 'translateX(calc(-150px + ' + move + 'px))'
        console.log(move, trans)
        // console.log('e.pageX', e.pageX)
        $('.s3pic').css('transform', trans)
      })
    } else {
      console.log('=========MB=========')
      setTimeout(() => {
        document.getElementById('box3').scrollLeft = 240
      }, 1000)
    }
  },

  computed: {}
}
</script>
